'use client';

import { useState } from 'react';
import { useRouter } from 'next/router'; // Si estás usando Next.js
import { IoPersonSharp, IoLockClosed, IoLogIn } from 'react-icons/io5';

export default function LoginPage() {
  const router = useRouter();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
  e.preventDefault();

  // Validación básica de campos
  if (!username || !password) {
    console.error('Usuario y contraseña son requeridos');
    return;
  }

  try {
    const res = await fetch('http://localhost:3000/apilocal/usuarios', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ username, password }),
      cache: 'no-store',
      credentials: 'include' // Si necesitas manejar cookies
    });

    // Manejo detallado de errores HTTP
    if (!res.ok) {
      const errorData = await res.json().catch(() => null);
      const errorMessage = errorData?.message || `Error ${res.status}: ${res.statusText}`;
      console.error('Error en la respuesta del servidor:', errorMessage);
      // Puedes mostrar este error al usuario
      return;
    }

    const data = await res.json();

    // Validación del tipo de dato recibido
    if (typeof data !== 'number') {
      console.error('Tipo de dato inesperado en la respuesta:', typeof data);
      return;
    }

    // Redirección basada en el valor recibido
    const routes = {
      1: '/paginas/admin',
      2: '/paginas/profesor',
      3: '/paginas/alumno'
    };

    const route = routes[data as keyof typeof routes];
    
    if (route) {
      // Si estás en Next.js
      const router = useRouter();
      await router.push(route);
      
      // Si estás en React estándar:
      // window.location.href = route;
    } else {
      console.warn('Valor inesperado recibido:', data);
      // Puedes mostrar un mensaje al usuario
    }

  } catch (error) {
    console.error('Error en el login:', error);
    // Manejo de errores de red o parsing
    if (error instanceof Error) {
      // Puedes mostrar este error al usuario
      console.error('Detalles del error:', error.message);
    }
  }
};

  return (
    <div className="flex items-center justify-center min-h-screen bg-black text-[#00ffcc] font-mono">
      <div className="bg-[#111] p-8 rounded-xl shadow-lg border border-[#00ffcc33] w-full max-w-sm">
        <h2 className="text-2xl font-bold text-center mb-6">SPM</h2>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="flex items-center bg-black border border-[#00ffcc55] rounded-md px-3 py-2">
            <IoPersonSharp className="mr-2" />
            <input
              type="text"
              name="username"
              placeholder="> usuario"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              required
              className="bg-black outline-none text-[#00ffcc] w-full"
            />
          </div>
          <div className="flex items-center bg-black border border-[#00ffcc55] rounded-md px-3 py-2">
            <IoLockClosed className="mr-2" />
            <input
              type="password"
              name="password"
              placeholder="> contraseña"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
              className="bg-black outline-none text-[#00ffcc] w-full"
            />
          </div>
          <button
            type="submit"
            className="flex items-center justify-center w-full bg-[#00ffcc] text-black font-bold py-2 rounded-md hover:bg-[#00e6b8] transition"
          >
            <IoLogIn className="mr-2 text-3xl" /> {'Entrar'}
          </button>
        </form>
      </div>
    </div>
  );
}
